<template>
	<div id="activity-coupon-collect">
		<public-header :title="page_title"></public-header>
		<div class="content">
			<van-cell-group class="margin-10-t">
				<van-field v-model="activity.title" label="活动名称" placeholder="请输入活动名称" input-align="right" />
			</van-cell-group>
			<van-cell-group class="margin-10-t">
				<van-cell>
					<div slot="title" class="">开始时间</div>
					<div slot="right-icon" class="c-gray" @click="onToggleDatePicker('start')">
						{{this.datePicker.start ? this.datePicker.start: '点击选择'}}
						<van-icon name="arrow"></van-icon>
					</div>
				</van-cell>
				<van-cell>
					<div slot="title" class="">结束时间</div>
					<div slot="right-icon" class="c-gray" @click="onToggleDatePicker('end')">
						{{this.datePicker.end ? this.datePicker.end: '点击选择'}}
						<van-icon name="arrow"></van-icon>
					</div>
				</van-cell>
			</van-cell-group>
			<div class="activity-box margin-10-t">
				<van-cell-group>
					<van-field v-model="activity.amount" label="券包总数" placeholder="请输入券包总数" input-align="right" />
				</van-cell-group>
				<van-radio-group v-model="activity.type_limit" v-if="filter.items.type == 'couponCollect'">
					<van-cell-group class="margin-10-t">
						<van-field v-model="activity.condition" label="面向人群" input-align="right" />
						<van-cell title="新老用户通用" clickable @click="onSelectGroup(1)" class="no-fix">
							<van-radio :name="1" />
						</van-cell>
						<van-cell title="新用户" clickable @click="onSelectGroup(2)" class="no-fix">
							<van-radio :name="2" />
						</van-cell>
					</van-cell-group>
				</van-radio-group>
				<van-cell-group class="margin-10-t" v-else-if="filter.items.type == 'couponGrant'">
					<van-field v-model="activity.condition" label="返券条件" placeholder="请输入满足返券金额" input-align="right" />
				</van-cell-group>
				<div class="coupon-container" v-if="coupons.length > 0">
					<div class="coupon-detail" v-for="(item, index) in coupons" :key="index">
						<div class="coupon-operation">
							<div class="btn-coupon-edit" @click="onEditCoupon(index)">编辑</div>
							<div class="btn-coupon-edit" @click="onDelCoupon(index)">删除</div>
						</div>
						<div class="coupon-amount">
							<div class="discount-amount">{{item.discount}}</div>
							<div class="font-12 c-gray padding-10-t">满{{item.condition}}可用</div>
						</div>
						<div class="coupon-term">
							领取后
							<span>{{item.use_days_limit}}</span>
							天内有效
						</div>
					</div>
				</div>
				<div class="add-coupon flex border-1px-tb" @click="onToggleCoupon">
					<div class="icon icon-add"></div>
					<div>添加优惠券</div>
				</div>
			</div>
			<div class="padding-15">
				<van-button size="normal" block class="font-16 bg-info" @click="onSubmit()">确认并保存</van-button>
			</div>
		</div>
		<iloading v-if="showPreLoading"></iloading>
		<!--时间弹出层-->
		<van-popup class="popup-time" v-model="datePicker.status" position="bottom">
			<van-datetime-picker
					v-model="datePicker.currentDate"
					type="datetime"
					:min-data="datePicker.currentDate"
					:max-date="datePicker.maxDate"
					@confirm="onConfirmTime"
					@cancel="onToggleDatePicker"
			/>
		</van-popup>
		<!--创建优惠券弹出层-->
		<van-popup v-model="popupShow.coupon" position="bottom">
			<div class="popup-title">
				<span class="c-info" @click="onToggleCoupon">取消</span>
				<span>添加优惠券</span>
				<span class="c-info" @click="onAddCoupon">确定</span>
			</div>
			<div class="popup-content">
				<van-cell-group>
					<van-field v-model="coupon.discount1" label="优惠券金额" placeholder="请输入优惠券金额" input-align="right" />
					<van-field v-model="coupon.condition1" label="满多少元可用" placeholder="请输入使用条件" input-align="right" />
					<van-field v-model="coupon.use_days_limit1" label="领取有效期" placeholder="请输入有效期" input-align="right" />
				</van-cell-group>
			</div>
		</van-popup>
	</div>
</template>

<script>
	import publicHeader from "@/components/header"
	import {getData, setData} from "@/controller/funcCommon"
	export default {
		components: {
			publicHeader,
		},
		data() {
			return {
				page_title: '',
				datePicker: {
					type: '',
					maxDate: new Date(2050, 12),
					currentDate: new Date(),
					status: false,
					start: '',
					end: '',
				},
				coupon: {
					condition1: 0,
					discount1: 0,
					use_days_limit1: 0,
				},
				popupShow: {
					coupon: false,
					time: false,
				},
				coupons: [],
				filter: {
					items: {
						type: ''
					}
				},
				activity: {
					title: '',
					amount: '',
					type_limit: 1,
					condition: '',
				},
				now_couponIndex: -1,
				showPreLoading: true
			}
		},
		methods: {
			onLoad() {
				var that = this;
				getData({
					vue: that,
					url: 'manage/activity/index/activity_coupon',
					data: {
						filter: this.filter.items
					},
					success: function(res) {
						that.islegal = true;
						that.page_title = res.page_title;
						let nowtime = that.util.formatDate(that.datePicker.currentDate);
						that.datePicker.start = that.datePicker.end = nowtime;
					},
					fail: function(res) {
						var url = '';
						if(res.errno == -1000) {
							url = that.util.getUrl({path: 'pages/activity/index'});
						}
						that.util.$toast(res.message, url, 1000);
					}
				})
			},
			onToggleDatePicker(type) {
				this.datePicker.status = !this.datePicker.status;
				if(type) {
					this.datePicker.type = type;
				}
			},
			onToggleCoupon(){
				if(!this.popupShow.coupon && this.filter.items.type == 'couponGrant') {
					if(this.coupons.length >= 1 && this.now_couponIndex == -1) {
						this.util.$toast('只能添加一张优惠券');
						return false;
					}	
				}
				this.popupShow.coupon = !this.popupShow.coupon;
				if(this.popupShow.coupon == false) {
					this.now_couponIndex = -1;
				}
			},
			onConfirmTime(value) {
				let time = this.util.formatDate(value);
				this.datePicker[this.datePicker.type] = time;
				this.onToggleDatePicker();
			},
			onSelectGroup(index) {
				this.activity.type_limit = index;
			},
			onAddCoupon() {
				var discount = parseFloat(this.coupon.discount1);
				if(discount <= 0) {
					this.util.$toast('请填写优惠金额');
					return false;
				}
				var condition = parseFloat(this.coupon.condition1);
				if(condition < discount) {
					this.util.$toast('使用条件不能小于优惠金额');
					return false;
				}
				var use_days_limit = parseFloat(this.coupon.use_days_limit1);
				if(use_days_limit <= 0) {
					this.util.$toast('请填写有效期');
					return false;
				}
				var temp = {
					condition: condition,
					discount: discount,
					use_days_limit: use_days_limit,
				};
				if(this.now_couponIndex >= 0) {
					this.coupons[this.now_couponIndex] = temp;
				} else {
					this.coupons.push(temp);
				}
				this.onToggleCoupon();
			},
			onEditCoupon(index) {
				this.coupon.condition1 = this.coupons[index].condition;
				this.coupon.discount1 = this.coupons[index].discount;
				this.coupon.use_days_limit1 = this.coupons[index].use_days_limit;
				this.now_couponIndex = index;
				this.onToggleCoupon();
			},
			onDelCoupon(index) {
				this.coupons.splice(index, 1);
			},
			onSubmit() {
				var that = this;
				if(!that.filter.items.type) {
					that.util.$toast('请先选择活动类型');
					return false;
				}
				if(that.activity.amount <= 0) {
					that.util.$toast('券包总数不能为空');
					return false;
				}
				if(that.filter.items.type == 'couponCollect' && !that.activity.type_limit) {
					that.util.$toast('请选择面向人群');
					return false;
				}
				if(that.coupons.length == 0) {
					that.util.$toast('请添加优惠券');
					return false;
				}
				var params = Object.assign(this.activity, {
						starttime: that.datePicker.start,
						endtime: that.datePicker.end,
						coupons: that.coupons,
				});
				setData({
					vue: that,
					url: 'manage/activity/index/activity_coupon',
					data: {
						type: that.filter.items.type,
						params: JSON.stringify(params)
					},
					redirect: that.util.getUrl({path: 'pages/activity/index'})
				});
			}
		},
		mounted() {
			this.onLoad();
		},
		created() {
			this.query = this.$route.query;
			if(this.query && this.query.type) {
				this.filter.items.type = this.query.type;
			}
		}
	}
</script>

<style>
	#activity-coupon-collect .content .activity-box .coupon-container{
		text-align: center;
		margin-top: 10px;
		background: #fff;
	}
	#activity-coupon-collect .content .activity-box .coupon-container .coupon-detail{
		display:inline-block;
		position:relative;
		width:80%;
		height:105px;
		margin: 10px 0;
		border:1px solid #ccc;
		border-radius:5px;
		color:#ccc;
	}
	#activity-coupon-collect .content .activity-box .coupon-container .coupon-detail:before{
		top: -8px;
		border-bottom: 1px solid #ccc;
		content: "";
		display: block;
		position: absolute;
		left: 24%;
		background-color: #fff;
		width: 18px;
		height: 18px;
		border-radius: 9px;
		z-index: 10;
	}
	#activity-coupon-collect .content .activity-box .coupon-container .coupon-detail:after{
		bottom: -10px;
		border-top: 1px solid #ccc;
		content: "";
		display: block;
		position: absolute;
		left: 24%;
		background-color: #fff;
		width: 18px;
		height: 18px;
		border-radius: 9px;
		z-index: 10;
	}
	#activity-coupon-collect .content .activity-box .coupon-container .coupon-detail .coupon-operation{
		position:absolute;
		right:10px;
		top:4px;
	}
	#activity-coupon-collect .content .activity-box .coupon-container .coupon-detail .coupon-operation .btn-coupon-edit{
		padding-left:5px;
		font-size:14px;
		color:#33aafc;
		display:inline-block;
	}
	#activity-coupon-collect .content .activity-box .coupon-container .coupon-detail .coupon-amount{
		position:relative;
		width:27%;
		padding:20px 0;
		border-right:1px dashed #ccc;
		text-align:center;
		float:left;
	}
	#activity-coupon-collect .content .activity-box .coupon-container .coupon-detail .coupon-amount .discount-amount{
		color:red;
		font-size:24px;
		padding: 4px 0;
	}
	#activity-coupon-collect .content .activity-box .coupon-container .coupon-detail .coupon-term{
		width:72%;
		line-height:105px;
		font-size:18px;
		text-align:center;
		float:left;
	}

	#activity-coupon-collect .content .activity-box .add-coupon{
		margin-top: 10px;
		width: 100%;
		height: 40px;
		background: #fff;
		color:#33aafc;
		justify-content: center;
	}
	#activity-coupon-collect .content .activity-box .add-coupon .icon-add{
		width:18px;
		height:18px;
		text-align:center;
		line-height:18px;
		font-size:16px;
		border-radius:50%;
		border:1px solid #33aafc;
		margin-right:5px;
	}
</style>


